.gpt3__blog-cointainer_ariticle {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    border-radius: 0.4rem;
}

.gpt3__blog-cointainer_ariticle_image {
    width: 100%;
    height: 100%;
    background: var(--color-footer);
       border-top-left-radius: 0.4rem;
    border-top-right-radius: 0.4rem;
}

.gpt3__blog-cointainer_ariticle_image img {
    width: 100%;
    height: 100%;
    padding: 1rem;
}

.gpt3__blog-cointainer_ariticle-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3__blog-cointainer_ariticle-content p {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
    color: #ffffff;
    opacity: 0.7;
}

.gpt3__blog-cointainer_ariticle-content p:last-child {
    cursor: pointer;
    opacity: 0.9;
}

.gpt3__blog-cointainer_ariticle-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: #ffffff;
    cursor: pointer;
    margin-bottom: 5rem;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-cointainer_ariticle-content h3{
        font-size: 18px;
        line-height: 24px;
    }
}
